import config from '../../config/config'

export default {
  getConfig: (method, url, data) => getConfig(method, url, data),
  getUrlSocketPrinter: () => getUrlSocketPrinter(),
  getUrlSystem: () => getUrlSystem()
}

const getUrlSocketPrinter = () => {
  return config.urlSocketPrinter
}

const getUrlSystem = () => {
  return config.urlSystem
}

const getConfig = (method, url, data) => {
  let token = JSON.parse(localStorage.getItem("api_auth")).token
  let configAux
  if (!data) {
    configAux = {
      url: `${config.urlApi}${url}`,
      method,
      headers: {'Authorization': "Bearer " + token}
    }
  } else {
    configAux = {
      url: `${config.urlApi}${url}`,
      method,
      headers: {'Authorization': "Bearer " + token},
      data
    }
  }
  return configAux
}
