import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    printSystem: false,
    urlSystem: '',
    userLealSelected: null,
    alert: {
      typeAlert: 'success',
      msgAlert: '',
      statusAlert: false
    },
    allPrizes: [],
    prizes: [],
    prizeSelected: null,
    token: '',
    userSelected: null,
  },
  mutations: {
    setPrizes: (state, action) => {
      state.allPrizes = action;
      state.prizes = action;
      state.prizeSelected = null
    },
    setPrizeSelected: (state, action) => {
      state.prizeSelected = action
    },
    setUserSelected: (state, action) => {
      state.allPrizes = []
      state.prizes = []
      state.prizeSelected = null
      state.userSelected = action
    }
  }
})
