<template>
  <div class="containerItem">
    <div class="item">
      <div style="display: flex; width: 100%; padding-left: 5%;">
        <input placeholder="Cédula o teléfono" class="input-item" type="text" v-model="name" v-on:keyup.enter="buscar" />
        <div class="button success" @click="buscar" style="height: 30px; display: flex; align-items: center; justify-content: center;">Buscar</div>
      </div>
    </div>
    <div class="item">
      <div v-if="this.$store.state.userSelected" class="user-selected" style="widht: 100%;">
        <div class="title">
          {{`${$store.state.userSelected.cedula} - ${$store.state.userSelected.fullname}`}}
        </div>
        <font-awesome-icon
          icon="times"
          :style="{ color: 'red', cursor: 'pointer' }"
          size="lg"
          @click="deseleccionar()"
        />
      </div>
    </div>
    <div class="users">
      <div
        v-for="(user, index) in users"
        :key="index"
        :class="{user: true, 'selected-user': userLocalSelected && userLocalSelected.uid === user.uid}"
        @click="select(user)"
      >
        <div class="title">{{`${user.cedula} - ${user.fullname}`}}</div>
      </div>
    </div>
    <div class="buttons">
      <div class="button cancel" @click="redimir()">Redimir Premio</div>
      <div class="button success" @click="seleccionar()">Buscar Premios</div>
    </div>
    <loading :active.sync="isLoading"></loading>
  </div>
</template>

<script>
  import helperConfig from "../../helpers/config";
  import helperAlert from "../../helpers/alert";
  import axios from "axios";
  import loading from "vue-loading-overlay";
  import { mapMutations } from "vuex";

  export default {
    name: "Users",
    components: {
      loading
    },
    data() {
      return {
        conn: {},
        isLoading: false,
        name: "",
        users: [],
        userLocalSelected: null,
      };
    },
    methods: {
      ...mapMutations(["setPrizes", "setUserSelected"]),
      getConfig: helperConfig.getConfig,
      getUrlSocketPrinter: helperConfig.getUrlSocketPrinter,
      setAlert: helperAlert.setAlert,
      select(user) {
        this.userLocalSelected = user;
      },
      deseleccionar() {
        this.setUserSelected(null);
      },
      seleccionar() {
        if (!this.userLocalSelected) {
          this.setAlert("Por favor seleccione a un usuario Leal");
          return;
        }

        this.setUserSelected(this.userLocalSelected);
        this.getPrizes();
      },
      async getStatusApertura() {
        try {
          const res = await axios(this.getConfig("get", "/box/verify-state"));
          return res.data.status;
        } catch (error) {
          return false;
        }
      },
      async redimir () {
        if (!this.$store.state.userSelected || !this.$store.state.prizeSelected) {
          this.setAlert("Por favor seleccione un usuario Leal y un premio para redimir");
          return;
        } else if (await !this.getStatusApertura()) {
          this.setAlert("Por favor haz apertura de caja");
          return;
        }

        this.isLoading = true;

        try {
          const res = await axios(this.getConfig("post", '/puntos_leal/redeem_prizes', {
            user: this.$store.state.userSelected,
            prize: this.$store.state.prizeSelected
          }));
          if (res.data.code === 100) {
            if (await this.process(res.data.product, res.data.warehouse, res.data.puntos, res.data.puntos_activos)) {
              this.setAlert('Premio redimido correctamente', true);
            } else {
              this.setAlert('El premio fue redimido. Pero hubo un error al procesar la factura.');
            }

            this.setUserSelected(null);
          } else {
            this.setAlert(res.data.message);
          }
        } catch (error) {
          this.setAlert(error);
        }
        
        this.isLoading = false;
      },
      async buscar() {
        if (this.name === "") {
          return;
        }

        this.isLoading = true;

        try {
          const res = await axios(this.getConfig("get", `/puntos_leal/search_user/${this.name}`));
          const data = res.data;

          if (data) {
            this.users = data.data;
          }
        } catch (error) {
          this.setAlert(error);
        }

        this.isLoading = false;
      },
      async getPrizes() {
        this.isLoading = true;

        try {
          let data = [];
          const res = await axios(this.getConfig("get", `/puntos_leal/prizes/${this.$store.state.userSelected.uid}`));
          if (res.data.code === 100) {
            data = res.data.data.filter(prize => {
              return prize.estado === "activo"
            })
          } else {
            this.setAlert(res.data.message);
          }

          this.setPrizes(data)
        } catch (error) {
          this.setAlert(error);
        }

        this.isLoading = false;
      },
      async process(product, warehouse, puntos, puntos_activos) {
        const state = this.$store.state;
        const data = {
          zone: "-1",
          table: "1557262347865",
          products: [{
            id: product.id,
            quantity: 1,
            additions : [],
            modifications: []
          }],
          payments: [],
          store_id: warehouse.id,
          total_payment : 0,
          total_received: 0,
          total_change: 0,
          selected_domicile: "0",
          selected_domicile_phone: null,
          selected_domicile_address: null,
          customer: null,
          type_process: "quick-service",
          total_discount: product.price.total,
          type_discount: "percent",
          discount: 100,
          total_propine: 0,
          note_invoice: "Puntos leal",
          note_command: "Puntos leal",
          order_consecutive: "300",
          quick_service_command: "si",
          user_puntos_leal: null
        };

        try {
          const res = await axios(this.getConfig("post", "/checkout/process", data));
          const _data = res.data;
          if (_data.status) {
            if (_data.fast_print_new && !state.printSystem) {
              let fast_print_data_new = _data.fast_print_data_new;
              fast_print_data_new.puntos = {
                puntos_activos,
                puntos: 0
              };
              fast_print_data_new.linea1 = `Puntos Leal redimidos: ${puntos}`;
              fast_print_data_new.linea2 = 'Factura redimida con Puntos Leal';
              fast_print_data_new.linea3 = '';

              this.sendDataToSocket(JSON.stringify(fast_print_data_new));
            } else {
              this.$modal.show("system");
              state.urlSystem = _data.id_sale;
            }

            return true;
          } else {
            return false;
          }
        } catch (error) {
          return false;
        }
      },
      sendDataToSocket(data) {
        this.conn.send(data);
      },
      openConnection() {
        //this.conn = window.WebSocket || window.MozWebSocket;
        if (this.conn.readyState === undefined || this.conn.readyState > 1) {
          this.conn = new WebSocket(this.getUrlSocketPrinter());
          var app = this;

          this.conn.onopen = function() {
            app.conn.send("Connection Established Confirmation");
          };

          this.conn.onmessage = function() {
            //document.getElementById("content").innerHTML = event.data;
          };

          this.conn.onerror = function() {
            console.log("Web Socket Error");
            app.$store.state.printSystem = true;
          };

          this.conn.onclose = function() {
            console.log("Web Socket Closed");
          };
        }
      },
    },
    mounted() {
      this.openConnection();
    }
  };
</script>

<style scoped>
  .title {
    text-align: left;
  }

  .user {
    display: flex;
    height: 10%;
    border-bottom: .5px #ddd solid;
    align-items: center;
    padding: 0 10%;
    justify-content: space-between;
  }

  .user-selected {
    display: flex;
    width: 85%;
    height: 70%;
    border: .5px #ddd solid;
    align-items: center;
    padding: 0 5%;
    justify-content: space-between;
  }

  .selected-user {
    background-color: #61ce70;
    color: #fff;
  }

  .users {
    height: 70%;
    overflow-y: auto;
    border-bottom: 1px #ddd solid;
  }

  .containerItem {
    height: 100%;
    display: flex;
    flex-direction: column;
    border-left: 1px #ddd solid;
  }

  .item {
    display: flex;
    height: 10%;
    width: 100%;
    justify-content: center;
    align-items: center;
    border-bottom: 1px #ddd solid;
  }

  .input-item {
    border-radius: 17px;
    height: 30px;
    width: 93%;
    border: 1px solid #ddd;
    line-height: 1.4;
    font-size: 1em;
    padding-left: 1em !important;
  }

  .buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10%;
  }

  .button {
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    margin: 0px 1em;
    color: #fff;
  }

  .cancel {
    background-color: #ccc;
  }

  .success {
    background-color: #61ce70;
  }
</style>
