<template>
  <div class="containerItem">
    <input type="text" v-model="search" class="input" placeholder="Por favor digité su busqueda">
  </div>
</template>

<script>
  export default {
    name: 'SearchPrize',
    data() {
      return {
        search: ''
      }
    },
    watch: {
      search(value) {
        if (value === '') {
          this.$store.state.prizes = this.$store.state.allPrizes
        } else {
          const filter = this.$store.state.allPrizes.filter((prize) => {
            const premio = prize.premio ? prize.premio.toUpperCase(): ''
            
            return premio.includes(value.toUpperCase())
          })
          this.$store.state.prizes = filter
        }
      }
    }
  };
</script>

<style scoped>
  .containerItem {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
  }

  .input {
    border-radius: 20px;
    background-color: #eeeeee;
    height: 30px;
    padding: 0 2%;
    border: 0;
    width: 90%;
  }
</style>
