<template>
  <div :class="{prize: true, select: $store.state.prizeSelected && $store.state.prizeSelected.id_premio === prize.id_premio}" @click="setPrizeSelected(prize)">
    <img alt="Vendty" class="image" :src="getImage(prize.imagen)" @error="replaceByDefault" />
    <div class="title-container" align-center>
      <p class="title">{{fixName(prize.premio)}}</p>
    </div>
    <p class="price">{{prize.puntos}}</p>
  </div>
</template>

<script>
  import { mapMutations } from "vuex";

  export default {
    name: "Prize",
    props: ["prize"],
    methods: {
      ...mapMutations(["setPrizes", "setPrizeSelected"]),
      replaceByDefault(e) {
        e.target.src = require("../../assets/placeholder.png");
      },
      fixName(name) {
        name = name.length > 60 ? name.substring(0, 60) : name;
        name = name.toUpperCase();

        return name;
      },
      getImage(image) {
        return image && image !== ""
          ? image
          : require("../../assets/placeholder.png");
      }
    }
  };
</script>

<style scoped>
  .number-items {
    position: absolute;
    z-index: 1;
    background-color: #61ce70;
    color: #fff;
    top: 0;
    right: 0;
    margin-right: 0.2em;
    margin-top: 0.2em;
    border-radius: 50%;
    padding: 0.1em 0.4em;
  }

  .title-container {
    background-color: #eee;
    height: 50px;
    padding: 1px;
  }

  .title {
    color: #777;
    font-size: 0.75em;
    font-weight: bold;
  }

  .price {
    margin-top: 0;
    color: #5cb85c;
    margin-bottom: 0;
    position: relative;
    top: -4px;
    padding-top: 0.5em;
  }

  .prize {
    width: 100%;
    border-radius: 10px;
    border: 1px solid #ddd;
    margin-top: 10px;
    margin-bottom: 5px;
    cursor: pointer;
    position: relative;
  }

  .image {
    width: 100%;
    max-width: 97%;
    max-height: 85px;
    border-top-left-radius: 10%;
    border-top-right-radius: 10%;
  }

  .select {
    border-color: green;
  }
</style>
