<template>
  <div class="modal-alert">
    <sweet-modal icon="success" ref="success">{{$store.state.alert.msgAlert}}</sweet-modal>
    <sweet-modal icon="error" ref="error">{{$store.state.alert.msgAlert}}</sweet-modal>
  </div>
</template>

<script>
  export default {
    name: 'ModalAlert',
    watch: {
      getStatus() {
        if (this.$store.state.alert.typeAlert === 'success') {
          const ref = this.$refs;
          this.$refs.success.open();
          setTimeout(function(){
            ref.success.close();
          }, 3000);
        } else {
          this.$refs.error.open()
        }
      }
    },
    computed: {
      getStatus() {
        return this.$store.state.alert.statusAlert
      }
    }
  };
</script>

<style scoped>
  .sweet-modal-overlay {
    background: none;
  }
</style>
