<template>
  <div class="listPrize">
    <div v-for="(items, index1) in Partition($store.state.prizes, 4)" :key="index1" class="flex">
      <div v-for="(item, index2) in items" :key="index2" class="width30">
        <Prize :prize="item" />
      </div>
    </div>
  </div>
</template>

<script>
  import Prize from "./Prize.vue";
  import "vue-loading-overlay/dist/vue-loading.css";

  export default {
    name: "listPrize",
    components: {
      Prize
    },
    methods: {
      Partition(items, size) {
        let output = [];
        for (let i = 0; i < items.length; i += size) {
          output[output.length] = items.slice(i, i + size);
        }
        return output;
      },
    }
  };
</script>

<style scoped>
  .listPrize {
    min-height: 100%;
    height: 100%;
    overflow-y: auto;
  }

  .flex {
    display: flex;
  }

  .width30 {
    width: 23%;
    margin: 1%;
  }
</style>
